
import './App.css';

import React, { Component } from 'react'
import Navbar from './Component/Navbar';
// import Newsitem from './Component/Newsitem';
import News from './Component/News';
import {
  BrowserRouter as Router,
  Switch,Route,
} from "react-router-dom";

export default class App extends Component {
  render() {
    return (
      <div>
      <Router>
        <Navbar/>
        <Switch>
        <Route exact path="/"><News key="general" pageSize={3} category="general"/></Route>
        <Route exact path="/business"><News key="business" pageSize={3} category="business"/></Route>
        <Route exact path="/entertainment"><News key="entertainment" pageSize={3} category="entertainment"/></Route>
        <Route exact path="/technology"><News key="technology" pageSize={3} category="technology"/></Route>
        <Route exact path="/sports"><News key="sports" pageSize={3} category="sports"/></Route>
        <Route exact path="/health"><News key="health" pageSize={3} category="health"/></Route>
        </Switch>
      </Router>
      </div>
    )
  }
}

